* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face{
  font-family: 'fluid';
  src: url('../fonts/Anonymous_Pro.ttf');
}
 @font-face{
font-family: 'fgr';
src: url('../fonts/Amagro-bold.ttf');
}
@font-face{
  font-family: 'text';
  src: url('../fonts/Cormorant-Light.otf');
}

/* ----------------------MENU---------------------- */
.conteiner__menu{
padding: 40px;
width: 90%;
position: fixed;
z-index: 100;
}
.fluid__menu li{
  display: inline;
  font-family: 'fgr';
  font-size: 2rem;
  padding: 0 5vw 0 5vw;
  float: right;
}
a{
  text-align: center;
  text-decoration: none;
  color: #fff;
}

.c::after{
  display: block;
  position: absolute;
  text-align: center;
  content: "ontacto";
  color: white;
  transform: scale(0) ;
  transition: all 300ms ease-in-out;
}
.c:hover::after{
  transform: scale(1);
}

.q::after{
  display: block;
  position: absolute;
  text-align: center;
  content: "uienes";
  color: white;
  transform: scale(0) ;
  transition: all 300ms ease-in-out;
}
.q:hover::after{
  transform: scale(1);
}

.i::after{
  display: block;
  position: absolute;
  text-align: center;
  content: "nicio";
  color: white;
  transform: scale(0) ;
  transition: all 300ms ease-in-out;
}
.i:hover::after{
  transform: scale(1);
}




/* ----------------------general---------------------- */




.App1{
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
