.fluid__intro{
  display: flex;
  flex-direction: column;
  justify-content: center;
  /*   background-color: red; */
  height: 80vh;
  width: 80vw;
  margin-top: 15vh;
  margin-bottom: 10vh;
  position: fixed;
}
.fluid__logo{
  position: fixed;
  width: 10vw;
  transform: translateX(-50%);
  z-index: 1000;
}

.fluid__somos{
  width: 12vw;
  margin-left: 50%;
  opacity: 0;
/*   transform: translateY(30px); */
  animation: aparecer  500ms ease-in-out; 
  animation-delay: 1000ms;
  animation-fill-mode: forwards;
}

.fluid__tittle__text{
  display: block;
  font-family: 'fluid';
  font-size: 7vw;
  color: white;
  margin-left: 50%; 
  opacity: 0;
  text-decoration: line-through solid;
  animation: aparecer  500ms ease-in-out; 
  animation-delay: 1500ms;
  animation-fill-mode: forwards;
}

.show{
  animation: aparecer  500ms ease-in; 
  animation-delay: 400ms;
  animation-fill-mode: forwards;
}
@keyframes aparecer {
  0% {
    opacity: 0;
    transform: translateY(70px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}


.fluid__texto__FGR{
  height: 100vh;
  padding-top: 25vh;  
  font-family: 'fgr';
  font-size: 4vw;
  line-height: 5.5vw;
  color: white;
}

/* .fgr{
  animation: flash infinite 1000ms ease-in-out;  
} */
.show2{
  animation: aparecer  500ms ease-in-out, flash infinite 1000ms ease-in-out; 
  animation-delay: 400ms;
  animation-fill-mode: forwards;
}

.fgr1, .fgr2, .fgr3, .fgr4, .fgr5,.fgr6 {  
  opacity: 0;
  animation-fill-mode: forwards;
}

@keyframes flash {
  0% {
    text-shadow: transparent 0px 4px 0px, transparent 0px 8px 0px;
  }
  33% {
    text-shadow: transparent 0px 4px 0px, transparent 0px 8px 0px;
  }
  34% {
    text-shadow: red 0px 4px 0px, transparent 0px 8px 0px;
  }
  65% {
    text-shadow: red 0px 4px 0px,transparent 0px 8px 0px;
  }
  66% {
    text-shadow: red 0px 4px 0px,#fecd1a 0px 8px 0px;
  }
  100% {
    text-shadow: red 0px 4px 0px,#fecd1a 0px 8px 0px;
  }
}


/* ------------------Seccion3------------------ */


.img1{
/*   clip-path: polygon(75% 0%, 100% 50%, 75% 100%, 0% 100%, 25% 50%, 0% 0%); */
}
.fluid__part1{
  width: 80vw;
}

.fluid__img1{
  opacity: 0;
  margin-left: 35vh;
  height: 70vh;
  position: relative;
  box-shadow: rgba(0, 0, 0, 0.8) 10px 10px 30px;
  animation-fill-mode: forwards;
}

.fluid__img1::before{
  content: url('../img/img1.jpg');
  clip-path: polygon(75% 0%, 100% 50%, 75% 100%, 0% 100%, 25% 50%, 0% 0%);
  position: absolute;
  left: 10vh;
  bottom: -5vw;
}

.fluid__img1::after{
  content: 'El cuerpo es el referente de nuestra identidad.';
  font-family: 'text';
  font-size: 2rem;
  color: white;
  width: 30vw;
  position: absolute;
  right: -20%;
  top: 50%;
}

