@media only screen and (max-width: 600px) {

  .conteiner__menu{
    padding: 5vh 0px 0px 0px;
    width: 10%;
    height: 40vh;
    position: fixed;
    z-index: 100;
    right: 23%;

    }
    .fluid__menu li{
/*       display: inline-block; */
      font-family: 'fgr';
      font-size: 1rem;
      padding: 1vw;
      margin: 3px;
      float: left;
    }

    .c::after, .q::after, .i::after{
      display: inline;
    }
    
    .quienes__info{
      width: 70vw;
      margin-top: 3vh;
      line-height: 3vh;
      font-size: 3vh;
    }
  .fluid__img__photo{
    width: 70vw;

  }
  .fluid__img1, .fluid__img2, .fluid__img3, .fluid__img4{
    margin-left: 15vw;
    padding-left: 0vw;  
    top: 0vh;
    height: 70vh;
  }
  
  .fluid__img1::before, .fluid__img2::before, .fluid__img3::before, .fluid__img4::before{
    display:none;
  }
  
  .fluid__img1::after, .fluid__img2::after, .fluid__img3::after, .fluid__img4::after{
    font-size: 2rem;
    line-height: 2rem;
    width: 30vw;
    position: absolute;
    right: 20%;
    top: 50%;
  }

  .fluid__logo{
    width: 20vh;
  }
  
  .fluid__somos{
    width: 22vw;
    margin-left: 20%;
  }
  
  .fluid__tittle__text{
    font-size: 7vh;
    margin-left: 20%; 
  }



  .fluid__texto__FGR{
    padding-top: 30vh;  
    font-family: 'fgr';
    font-size: 15vw;
    line-height: 25vw;
  }

  
  .fgr2, .fgr3, .fgr4, .fgr5,.fgr6 {  
    display: none;
  }
}